.fickleflightIcon {
  position: relative;
  width: 174px;
  height: 32px;
}
.explore,
.hotels,
.offers {
  position: relative;
}
.hotels,
.offers {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  font-size: var(--font-size-sm);
  font-family: var(--font-roboto);
  color: var(--color-cornflowerblue-100);
  text-align: left;
  display: inline-block;
}
.offers {
  color: var(--color-darkslategray-200);
}
.navigationMenu {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  gap: var(--gap-11xl);
}
.hamburgerMenuIcon {
  display: none;
}
.hamburgerMenuIcon,
.notificationBellIcon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.avatarIcon {
  border-radius: var(--br-19xl);
  width: 36px;
  height: 36px;
  object-fit: cover;
}
.accountSection {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-xs);
}
.navigationRight {
  justify-content: flex-end;
  gap: var(--gap-17xl);
}
.navigationRight,
.topContainer,
.topHeader {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.topContainer {
  flex: 1;
  justify-content: space-between;
}
.topHeader {
  align-self: stretch;
  background-color: var(--color-white);
  height: 77px;
  padding: var(--padding-3xl) var(--padding-61xl);
  box-sizing: border-box;
  justify-content: center;
  position: sticky;
  width: 100%;
  top: 0;
  background: #fff;
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--color-darkslategray-200);
  font-family: var(--font-roboto);
}
.hotelsTagline {
  position: relative;
  letter-spacing: 0.02em;
  line-height: 48px;
  display: inline-block;
  width: 1280px;
}
.recommended {
  position: relative;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  font-weight: 500;
}
.filterOption {
  background-color: var(--color-white);
  color: var(--color-cornflowerblue-100);
}
.filterOption,
.filterOption1 {
  border-radius: var(--br-8xl);
  border: 1px solid var(--color-white);
  display: flex;
  flex-direction: row;
  padding: var(--padding-2xs) var(--padding-2xl);
  align-items: center;
  justify-content: center;
}
.filterOptions,
.title {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.filterOptions {
  flex-direction: row;
  text-align: center;
  font-size: var(--font-size-base);
  font-family: var(--font-roboto);
}
.title {
  position: absolute;
  top: 50px;
  left: 0;
  width: 1440px;
  flex-direction: column;
  padding: 0 var(--padding-61xl);
  box-sizing: border-box;
}
.banner,
.header {
  align-self: stretch;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.banner {
  position: relative;
  height: 193px;
  background-image: url(/public/banner@3x.png);
}
.header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  background-image: url(/public/header@3x.png);
  font-size: var(--font-size-23xl);
  font-family: var(--font-baloo-bhai);
}
.results {
  flex: 1;
  text-transform: uppercase;
  font-weight: 500;
}
.filtersText,
.results {
  position: relative;
  letter-spacing: 0.04em;
}
.filters,
.resultsSumamry {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.filters {
  align-items: center;
  gap: var(--gap-sm);
  font-size: var(--font-size-base);
}
.resultsSumamry {
  align-self: stretch;
  align-items: flex-start;
}
.resultsImageIcon {
  align-self: stretch;
  position: relative;
  border-radius: var(--br-3xs) 0 0 var(--br-3xs);
  max-height: 100%;
  width: 260px;
  object-fit: cover;
}
.hotelName,
.hotelType {
  position: relative;
  display: inline-block;
}
.hotelType {
  letter-spacing: 0.02em;
  color: var(--color-gray-100);
  width: 146.92px;
}
.hotelName {
  font-size: var(--font-size-3xl);
  letter-spacing: 0.04em;
  text-transform: capitalize;
  width: 232.87px;
}
.staricon {
  position: relative;
  width: 16px;
  height: 16px;
}
.rating1 {
  position: relative;
  letter-spacing: 0.04em;
  font-weight: 500;
}
.stars {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-7xs);
}
.reviews {
  position: relative;
  letter-spacing: 0.04em;
  color: var(--color-cornflowerblue-100);
}
.resultAndVideo,
.review {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.resultAndVideo {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-7xs);
}
.night,
.price1 {
  position: relative;
  letter-spacing: 0.02em;
}
.price1 {
  text-transform: capitalize;
}
.night {
  font-size: var(--font-size-sm);
}
.pricePerNight {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
  gap: var(--gap-12xs);
}
.searchFlights {
  position: relative;
  font-size: var(--font-size-lg);
  letter-spacing: 0.04em;
  font-weight: 500;
  font-family: var(--font-roboto);
  color: var(--color-white);
  text-align: center;
}
.viewDetailsButton {
  cursor: pointer;
  border: 0;
  padding: var(--padding-smi) var(--padding-9xl);
  background-color: var(--color-cornflowerblue-100);
  border-radius: var(--br-7xs);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.priceAndCta {
  flex-direction: column;
  gap: var(--gap-xs);
  font-size: var(--font-size-3xl);
}
.priceAndCta,
.resultsCard,
.resultsDetails {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.resultsDetails {
  flex: 1;
  flex-direction: column;
  padding: var(--padding-mini) var(--padding-xs) var(--padding-mini) 0;
  gap: var(--gap-12xl);
}
.resultsCard {
  align-self: stretch;
  border-radius: var(--br-3xs);
  background-color: var(--color-cornflowerblue-300);
  flex-direction: row;
  gap: var(--gap-base);
}
.mobileMap {
  border: 0;
  align-self: stretch;
  position: relative;
  height: 600px;
  display: none;
}
.resultCardsCol {
  width: 560px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-xs);
}
.desktopMap {
  border: 0;
  align-self: stretch;
  flex: 1;
  position: relative;
}
.hotelResults,
.resultsSection {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.resultsSection {
  flex-direction: row;
  gap: var(--gap-4xl);
  font-size: var(--font-size-base);
}
.hotelResults {
  flex-direction: column;
  padding: var(--padding-31xl) var(--padding-61xl);
  gap: var(--gap-xl);
  color: var(--color-darkslategray-300);
}
.formTitle,
.formTitleSubtext {
  align-self: stretch;
  position: relative;
}
.formTitleSubtext {
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.formTitle {
  font-size: var(--font-size-11xl);
  color: var(--color-darkslategray-100);
}
.formHeadingSection {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-6xs);
}
.formDescription {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-lg);
  line-height: 34px;
  font-family: var(--font-roboto);
  color: var(--color-slategray);
  text-align: left;
}
.formText {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.destinationNameInput {
  border: 0;
  background-color: transparent;
  align-self: stretch;
}
.formFields {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.submit {
  position: relative;
  font-size: var(--font-size-mini);
  letter-spacing: 0.46px;
  line-height: 26px;
  text-transform: uppercase;
  font-weight: 500;
  font-family: var(--font-roboto);
  color: var(--color-white);
  text-align: center;
}
.searchButton {
  cursor: pointer;
  border: 0;
  padding: 0 var(--padding-3xs);
  background-color: var(--color-orange-200);
  border-radius: var(--br-9xs);
  width: 164px;
  height: 56px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.searchButton:hover {
  background-color: var(--color-darkgoldenrod-100);
}
.form {
  align-self: stretch;
  border-radius: var(--br-xs);
  background-color: var(--color-white);
  box-shadow: 0 0 24px rgba(0, 0, 0, 0.03);
  border: 1px solid var(--color-whitesmoke-200);
  display: flex;
  flex-direction: column;
  padding: var(--padding-9xl) var(--padding-11xl);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-mid);
}
.shareYourTravelsForm,
.subscribeSection {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.shareYourTravelsForm {
  width: 549px;
  padding: var(--padding-xl);
  box-sizing: border-box;
  justify-content: flex-start;
  gap: var(--gap-11xl);
  margin-left: auto;
  margin-right: auto;
  position: relative !important;
  left: auto !important;
}
.subscribeSection {
  align-self: stretch;
  overflow: hidden;
  padding: var(--padding-33xl) var(--padding-xl);
  justify-content: center;
  background-image: url(/public/subscribe-section@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.logoIcon {
  position: relative;
  width: 174px;
  height: 33px;
  overflow: hidden;
  flex-shrink: 0;
}
.fickleFlightDescription {
  align-self: stretch;
  position: relative;
  line-height: 27px;
}
.facebookIcon {
  position: relative;
  width: 30px;
  height: 30px;
  overflow: hidden;
  flex-shrink: 0;
}
.socialIcons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-xl);
}
.fickleflightBio {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-sm);
  margin-bottom: 40px !important;
}
.seperator {
  align-self: stretch;
  position: relative;
  border-right: 1px solid var(--color-gray-600);
  box-sizing: border-box;
  width: 1px;
}
.company {
  position: relative;
  line-height: 27px;
  font-weight: 500;
}
.howWeWork {
  position: relative;
  display: inline-block;
  width: 197px;
}
.companyLinks,
.companySection,
.moreSection {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.companyLinks {
  gap: var(--gap-xs);
  font-size: var(--font-size-base);
}
.companySection,
.moreSection {
  gap: var(--gap-2xl);
}
.footerLinks {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  font-size: var(--font-size-xl);
}
.footer,
.footerSection,
.hotelsPage {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.footer {
  align-self: stretch;
  background: linear-gradient(91.74deg, #0b5ba8 25.38%, #299bd8);
  flex-direction: row;
  padding: var(--padding-14xl) var(--padding-65xl);
  gap: var(--gap-25xl);
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
.footerSection,
.hotelsPage {
  flex-direction: column;
}
.footerSection {
  align-self: stretch;
  text-align: center;
  color: var(--color-cornflowerblue-200);
}
.hotelsPage {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-white);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 1200px) {
  .title,
  .topHeader {
    padding-left: var(--padding-21xl);
    padding-right: var(--padding-21xl);
    box-sizing: border-box;
  }
  .banner {
    padding: var(--padding-2xl) var(--padding-21xl) var(--padding-2xl) 41px;
    box-sizing: border-box;
  }
  .footer,
  .hotelResults {
    padding: var(--padding-21xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 960px) {
  .navigationMenu {
    display: none;
  }
  .hamburgerMenuIcon {
    display: flex;
  }
  .title,
  .topHeader {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
  }
  .banner {
    padding: var(--padding-2xl) var(--padding-5xl);
    box-sizing: border-box;
  }
  .mobileMap {
    display: flex;
    max-width: 1000px;
  }
  .resultCardsCol {
    width: 100% !important;
  }
  .desktopMap {
    display: none;
  }
  .hotelResults {
    padding: var(--padding-11xl) var(--padding-5xl);
    box-sizing: border-box;
  }
  .shareYourTravelsForm {
    padding: var(--padding-5xl) 0;
    box-sizing: border-box;
  }
  .fickleflightBio {
    flex: unset;
    align-self: stretch;
  }
  .seperator {
    display: none;
  }
  .footerLinks {
    flex: unset;
    align-self: stretch;
  }
  .footer {
    flex-direction: column;
    padding: var(--padding-21xl) var(--padding-5xl) var(--padding-11xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .navigationRight {
    display: flex;
  }
  .hotelsTagline {
    font-size: var(--font-size-13xl);
  }
  .recommended {
    font-size: var(--font-size-sm);
  }
  .resultsImageIcon {
    width: 100% !important;
    border-radius: 10px !important;
  }
  .resultsDetails {
    width: 100%;
    padding: var(--padding-mini);
    box-sizing: border-box;
    flex: unset;
    align-self: stretch;
  }
  .resultsCard {
    flex-direction: column;
  }
  .resultCardsCol {
    gap: var(--gap-xl);
  }
  .searchButton {
    width: 100%;
  }
  .shareYourTravelsForm {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    position: relative !important;
    left: auto !important;
    width: 100%;
  }
  .companySection {
    margin-bottom: 40px !important;
  }
  .footerLinks {
    flex-direction: column;
  }
  .footer {
    padding: var(--padding-11xl) var(--padding-5xl);
    box-sizing: border-box;
  }
}
