    width: 100%;
    padding-bottom: var(--padding-3xs);
    margin-bottom: 6px;
    flex: unset;
    align-self: stretch;
  }
  .flightOptions {
    width: 100%;
  }
  .formTitleGroup {
    flex-direction: column;
    align-items: flex-start;
  }
  .departureAutocomplete {
    flex: unset;
    align-self: stretch;
  }
  .inputsRow {
    flex-direction: column;
  }
  .searchButton {
    width: 100%;
  }
  .searchForm {
    margin-top: 20px;
  }
  .searchSection {
    width: 100%;
    top: calc(32% - 96.5px - 0px);
  }
  .airlineSection {
    flex-direction: row;
    width: 100% !important;
  }
  .flightduration {
    transform: scale(0.9);
  }
  .flightDetailsSection {
    flex: unset;
    align-self: stretch;
  }
  .separatorIcon {
    display: none;
  }
  .priceSection {
    width: auto;
    position: absolute;
    top: 30px;
    right: 18px;
  }
  .flightCard {
    flex-direction: column;
    gap: var(--gap-xl);
    padding: var(--padding-xl);
    box-sizing: border-box;
    position: relative;
  }
  .airlineIcon1 {
    transform: scale(0.8);
  }
  .shareYourTravelsForm {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    position: relative !important;
    left: auto !important;
    width: 100%;
  }
  .companySection {
    margin-bottom: 40px !important;
  }
  .footerLinks {
    flex-direction: column;
  }
  .footer {
    padding: var(--padding-11xl) var(--padding-5xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 350px) {
  .departureAirport,
  .departureTime {
    font-size: var(--font-size-base);
  }
  .departureDetails {
    width: 80px;
  }
  .travelTime {
    font-size: var(--font-size-smi);
  }
  .arrivalDetails {
    width: 80px;
  }
}
